.divider {
    background-color: #fff;
    height: 1px;
    margin-top: 1rem;
    width: 100%;
}

.temtem {
    background-color: #111;
    border: 1px solid #aaa;
    border-radius: 5px;
    margin: 0.5rem 1rem 0.5rem 0;
    min-width: 250px;
    padding: 0.25rem 0 1rem 0;
}

.temtem:hover {
    background-color: #333;
    border-color: #fff;
    color: #fff;
}

.temtem:hover .temtem-image {
    background-color: #ddd;
}

.temtem-attributes {
    text-align: center;
}

.temtem-image {
    width: 100%;
}

.temtem-image-container {
    border: 1px solid #666;
    border-radius: 50%;
    height: 120px;
    margin: 1rem auto 0 auto;
    overflow: hidden;
    width: 120px;
}

.temtem-name-container {
    font-size: 1.5rem;
    font-weight: 600;
}

.temtem-name-container p:nth-of-type(1) {
    font-size: 1.2rem;
    font-weight: 500;
    margin: .75rem 0 0 0;
}

.temtem-name-container p:nth-of-type(2) {
    margin: 0 0 .5rem 0;
}


.temtem-type {
    height: 40px;
    object-fit: contain;
    width: 40px;
}

.temtem-type-container p {
    font-size: .8rem;
    margin-top: 0;
}

.temtem-type-container p span:not(:last-of-type)::after,
.temtem-weaknesses p span:not(:last-of-type)::after {
    content: ', ';
}

.temtem-weaknesses > p  {
    font-size: .8rem;
    font-weight: 00;
    margin-top: 0;
}

.temtem-weaknesses-container > p {
    font-weight: 600;
    margin: 1.25rem 0 .5rem 0;
}