body {
    background-color: #000;
    color: #fff;
    font-family: 'Poppins', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

input:focus, 
select:focus {
    outline: none;
}

.temtem-app h1 {
    font-size: 1.7rem;
}

.temtem-container {
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
    justify-content: center;
}

.temtem-search-container {
    margin-bottom: 1.5rem;
    width: 100%;
}

.temtem-search-container input {
    width: calc(100% - 1.25rem);
}

.temtem-search-container input::placeholder {
    color: #aaa;
}

.temtem-search-container input,
.temtem-search-container select {
    background-color: #111;
    border: 1px solid #666;
    border-radius: 3px;
    color: #fff;
    display: block;
    margin: 0.5rem 1rem 0.25rem 0;
    padding: .75rem 0.5rem;
}

.temtem-search-container select {
    width: 100%;
}